import { Polygon } from '@react-google-maps/api'
import { RM } from '@speedlo/tools'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useQOrderReturnZones, ZoneTypeEnum } from '../../../graph/generated'
import { gpsToLatLng } from '../../../helpers/gps'
import { useLoadingCache } from '../../../hooks/useLoadingCache'
import { Colors } from '../../../styling'
import { useOrderListQueryControl } from '../hooks/useOrderListQueryControl'
import { TMapReturnZone } from '../order.types'

export const OrderMapReturnZones = observer(() => {
  const { branchesFilter: branches, noFilter } = useOrderListQueryControl()

  const getReturnZones = useLoadingCache<RoA<TMapReturnZone>>(noFilter)

  const { data, loading } = useQOrderReturnZones(
    { branches },
    {
      skip: noFilter,
    },
  )

  const zones = getReturnZones(loading, () => {
    return noFilter
      ? []
      : data.zones.filter(RM.pathEq(['zoneType', 'enum'], ZoneTypeEnum.Return))
  })

  if (noFilter || zones === null) {
    return null
  }

  const renderPolygon = (zone: TMapReturnZone) => {
    return (
      <Polygon
        path={zone.geoPolygon.map(gpsToLatLng)}
        options={{
          fillColor: Colors.greyMiddle,
          fillOpacity: 0.1,
          strokeWeight: 2,
          strokeColor: zone.zoneColor.color,
        }}
        editable={false}
        draggable={false}
      />
    )
  }

  return <>{zones.map(renderPolygon)}</>
})
