import { RM } from '@speedlo/tools'

import orderNotificationSound from '../../../assets/orderNotification.mp3'
import { useRoot } from '../../../atoms/RootProvider'
import { useIntervalSound } from '../../../hooks/useIntervalSound'
import { TOrder } from '../order.types'
import { useOrderListModel } from './useOrderListModel'

export function useOrderListNotification(getOrders: () => RoA<TOrder>) {
  const { config } = useRoot()
  const orderListModel = useOrderListModel()

  // sound should be played as long as there are new orders
  // and there is at least one order with detail closed
  const shouldPlayNotification = !RM.all(
    order => orderListModel.isOrderDetailOpen(order.id),
    getOrders(),
  )

  return useIntervalSound({
    soundUrl: orderNotificationSound,
    intervalSec: config.orderListSoundIntervalSec,
    shouldPlay: shouldPlayNotification,
  })
}
